import List from '../components/list';
import Display from '../components/display';

function Home() {
    var item;
    
    const updateInfo = ($item) => item = $item;

    return (
        <div style={{ fontFamily: 'helvetica' }}>
            <List updateInfo={updateInfo}></List>
            <Display item={item}></Display>
        </div>
    );
}
  
export default Home;